* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
}
@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.body {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  background-color: #ffffff;
  min-height: 100vh;
  display: grid;
  grid-column: 1fr;
  grid-template-rows: min-content 1fr min-content;
}
.body--scrolloff {
  overflow: hidden;
}
.header {
  height: 100px;
}
.footer {
  height: 100px;
}
.top-five {
  height: min-content;
}
.top-five__title {
  padding: 20px;
  text-align: left;
  font-weight: bold;
  font-size: 28px;
  text-transform: capitalize;
}
.top-five__list {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(5, 1fr);
  grid-row-gap: 20px;
}
.top-five__item {
  width: 100%;
  position: relative;
  display: grid;
  grid-template-columns: 180px 140px repeat(3, 1fr);
  grid-template-areas: 'i t b s m';
  color: #ffffff;
  background: #002d4a;
  background: linear-gradient(0deg, #002d4a 0%, rgba(0, 36, 61, 0.87438725) 100%);
}
.top-five__name-wrap {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 5px 10px;
  grid-area: t;
}
.top-five__name {
  font-size: 20px;
  font-weight: normal;
  text-align: center;
  text-transform: capitalize;
  text-align: left;
}
.top-five__currency-info {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  grid-area: i;
}
.top-five__currency-block {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
  grid-row-gap: 4px;
  justify-items: center;
  padding: 10px;
}
.top-five__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: blur(1px);
}
.top-five__currency-name {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: bold;
  font-size: 42px;
  text-transform: uppercase;
  color: #ffffff;
}
.top-five__price-title {
  font-size: 20px;
  font-weight: normal;
  text-transform: capitalize;
  color: #DCDCDC;
  align-self: end;
}
.top-five__price {
  width: 100%;
  text-align: center;
  font-size: 38px;
  font-weight: bold;
  color: #ffffff;
}
.top-five__price--error {
  font-size: 22px;
  color: tomato;
  opacity: 0.8;
}
.top-five__buy {
  border-right: 2px dotted #808080;
  border-left: 2px dotted #808080;
  grid-area: b;
}
.top-five__sell {
  border-right: 2px dotted #808080;
  grid-area: s;
}
.top-five__average {
  grid-area: m;
}
